import React from "react";
import { type IWeatherCurrent } from "../../types";
import { getValidDate } from "../../helpers";
import styles from "./currentWeather.module.css";

type CurrentWeatherProps = IWeatherCurrent;

const CurrentWeather: React.FC<CurrentWeatherProps> = (weather) => {
	return (
		<div className={styles.currentWeather}>
			<p className={styles.currentInfo}>{getValidDate(weather.time)}</p>
			<p className={styles.currentInfo}>
				  <span className={styles.currentInfoBold}>{weather.temperature2m}</span>
			</p>
			<p className={styles.currentInfo}>
				  <span className={styles.currentInfoBold}>{weather.apparentTemperature}</span>
			</p>
			<p className={styles.currentInfo}>
				  <span className={styles.currentInfoBold}>{weather.windSpeed10m}/</span>
			</p>
			<p className={styles.currentInfo}>
				  <span className={styles.currentInfoBold}>{weather.relativeHumidity2m}%</span>
			</p>
		</div>
	);
};

export default CurrentWeather;
